import React from "react";
import { Link } from "react-router-dom";

export default function KnowledgePoint( {module} ) {
  return (
    <div className="App">
      <h1 className="title">{module.name}</h1>
      <table className="kpTable">
        <thead>
          <tr>
            <th>No.</th> 
            <th>Operations</th> 
            <th>Difficulty</th> 
            <th>Length</th>
            <th>Start Evaluation</th>
          </tr>
        </thead>

        <tbody>
          {module.map((questionSet, index) => (
            <tr key={index}>
              <td>{questionSet.id}</td> 
              <td>{questionSet.name}</td> 
              <td>{questionSet.difficulty}</td> 
              <td>{questionSet.length}</td>
              <td>
                <Link to={`/quiz/${questionSet.id}`}>
                  <button className="startButton">Start</button>
                </Link>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
